              <v-sheet outlined class="rounded-lg">
                <v-btn v-if="$vuetify.theme.dark" v-show="loading == false" block class="rounded-lg text-capitalize subtitle-1" depressed @click="ShowSidebar" :color="MenuSideBar ? 'red darken-2 white--text' : ''"><v-icon left>mdi-menu</v-icon>Side Bar</v-btn>
                <v-btn v-else v-show="loading == false" block class="rounded-lg text-capitalize subtitle-1" depressed @click="ShowSidebar" :color="MenuSideBar ? 'red darken-2 white--text' : ''"><v-icon left>mdi-menu</v-icon>Side Bar</v-btn>
              </v-sheet>
