                <v-btn v-else v-show="loading == false" block class="rounded-lg text-capitalize subtitle-1" depressed @click="ShowDashboard" :color="MenuDashboard ? 'red darken-2 white--text' : $vuetify.theme.dark ? '' : ''"><v-icon left>mdi-view-dashboard</v-icon>Dashboard</v-btn>
