<script>
import api from "@/services/http";
  export default {
    data: () => ({
      fab: false,
      loading_simpan_menu: false,
      loading_tampilkan_menu: false,
      mobile:null,
      windowSize: {x: 0, y: 0},
      DialogUserMenu: false,
