        x.enabled = !!x.enabled
        let Menu = this.StringMenu[0].MenuDashboard
        Menu = Menu.replaceAll(',','')
        let data = [...Menu]
        let realdata = data
        if (x.enabled == false) {
        realdata = realdata.filter(e => e != x.key.toString())
        this.StringMenu[0].MenuDashboard = realdata.toString()
        }else{
          realdata.push(x.key)
          this.StringMenu[0].MenuDashboard = realdata.toString()
        }
      },
