          // console.log(res.data)
          this.Jabatan = res.data
          this.loading = false
          this.getUser()
				},
				err => {
					console.log(err);
				})
        }
      },
      getUser(){
        api.get('getdatauser?token='+this.token,{
          transformResponse: api.defaults.transformResponse.concat((data) => {
            this.loading = false
            data = this.$decrypt(data)
            data = data.filter(x => x.karyawan != null)
            this.UserData = data
          })
        },
				err => {
